* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, sans-serif;
  line-height: 1.4;
  color: #444;
  background: #fff;
  height: 100vh;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
}

.inner {
  width: 100%;
  padding: 7rem 10px 4rem;
}

.form-container {
  display: flex;
  width: 100%;
  max-width: 600px;
  display: flex;
  border-radius: 1px;
  height: 100%;
  justify-content: center;
}

.input-text {
  transform: translateX(5%);
  font-size: 1rem;
  font-weight: 400;
  width: 100%;
  max-width: 400px;
  border-radius: 10px;
  height: 45px;
  outline: none;
  border: none;
}

.input-text::placeholder {
  color: #000;
  text-align: center;
}

.btn {
  position: relative;
  left: -40px;
  border: none;
  padding: 13px 5px;
  margin-top: 8px;
  width: 50px;
  cursor: pointer;
  background-color: white;
}

.active-link {
  color: white;
  text-decoration: underline;
}

.navBar {
  position: relative;
}

.navBar button {
  position: fixed;
  left: 40px;
  top: 40px;
  z-index: 10;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
}

a {
  display: block;
  padding: 10px 40px;
  text-decoration: none;
  color: gray;
  font-weight: bold;
  outline: none;
}

.item-container {
  display: flex;
  justify-content: center;
}

.item-container:first-child {
  margin-top: 50px;
}

.item {
  background: #f4f4f4;
  margin: 5px;
  width: 100%;
  max-width: 500px;
  position: relative;
  word-break: break-all;
  padding: 30px 20px;
  cursor: pointer;
  list-style-type: none;
}

.item button {
  align-items: center;
  font-size: 13px;
  color: gray;
  background: #f1f3f4;
  border: none;
  font-weight: bold;
  cursor: pointer;
  float: right;
  outline: none;
  border-radius: 100px;
  height: 50px;
  width: 50px;
  margin: -10px 0 0 10px;
}

.textInput {
  width: 70%;
  padding: 10px;
  border: 1px solid #dfdfdf;
}
